<template>
  <div class="zjmx">
    <el-table :data="tableData" border style="width:54.3%; margin-left: 40px">
      <el-table-column prop="id" label="id" width="80"></el-table-column>
      <el-table-column prop="sj" label="" width="150"></el-table-column>
      <el-table-column prop="username" label="" width="160"></el-table-column>
      <el-table-column prop="money" label="" width="80"></el-table-column>
      <el-table-column prop="xfd" label="" width="80"></el-table-column>
      <el-table-column prop="lw" label="" width="150"></el-table-column>
      <el-table-column prop="num" label="" width="50"></el-table-column>
      <el-table-column prop="time" label="" width="150"></el-table-column>
    </el-table>
    <div class="fh">
      <el-button size="small" @click="fh">{{uid}}</el-button>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    uid: Number
  },
  data() {
    return {
      tableData: [
        {
          id: 112233,
          sj: 18617271159,
          username: "",
          money: 100,
          xfd: "",
          lw: "",
          num: 1,
          time: "2019-10-12 18:00"
        }
      ]
    };
  },
  created() {
    window.console.log(this.uid);
  },
  methods: {
    getSummaries(param) {
      const { columns } = param;

      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        //this.closeList
        switch (column.property) {
          case "money":
            sums[index] = this.money;
            break;
          case "num":
            sums[index] = this.num;
            break;
          default:
            break;
        }
      });

      return sums;
    },
    fh() {
      this.$emit("fh");
    }
  }
};
</script>
<style lang="scss" scoped>
.fh {
  margin-top: 30px;
  margin-left: 867px;
}
</style>